import React, {
  useContext,
  useState,
  useRef,
  FormEvent,
  useReducer,
  useEffect,
} from "react";
import {
  Box,
  Typography,
  TextField,
  Button,
  CircularProgress,
  FormControl,
  InputLabel,
  MenuItem,
  Select,
  LinearProgress,
} from "@mui/material";
import AddIcon from "@mui/icons-material/Add";
import DeleteIcon from "@mui/icons-material/Delete";
import useUrlParams from "../../../hooks/useUrlParams";
import { GridRowSelectionModel } from "@mui/x-data-grid";
import DataGridTable from "../../../components/DataGridTable";
import { pink } from "@mui/material/colors";
import { AuthContext } from "../../../components/AuthContext";
import { useMutation, useQuery, useQueryClient } from "react-query";
import Swal from "sweetalert2";
import { wait } from "../../../lib/wait";
import { throttle } from "lodash";
import { green } from "@mui/material/colors";
import CloseIcon from "@mui/icons-material/Close";
import SaveIcon from "@mui/icons-material/Save";
import { Dropdown } from "../../../components/DropdownButton";

const initialState = {
  Line: "Vehicle",
  Account: "",
  Type: "",
  Rate: "0.0000",
};

export const reducer = (state: any, action: any) => {
  switch (action.type) {
    case "UPDATE_FIELD":
      return {
        ...state,
        [action.field]: action.value,
      };
    default:
      return state;
  }
};

export const sublineColumn = [
  { field: "ID", headerName: "ID", width: 100 },
  { field: "Account", headerName: "Account", flex: 1 },
  { field: "Type", headerName: "Type", flex: 1 },
  { field: "Rate", headerName: "Rate", flex: 1 },
  { field: "createdAt", headerName: "Created At", width: 150 },
];
const queryKey = "subline-account";
export default function Rates() {
  const { searchParams, setSearchParams } = useUrlParams();
  const [state, dispatch] = useReducer(reducer, initialState);
  const { myAxios, user } = useContext(AuthContext);
  const [exportMode, setExportMode] = useState(false);
  const [loadingSearch, setLoadingSearch] = useState(false);
  const [save, setSave] = useState(searchParams.get("selected") !== "");
  const [addNew, setAddNew] = useState(searchParams.get("selected") !== "");
  const [deleting, setDeleting] = useState(searchParams.get("selected") !== "");
  const [rows, setRows] = useState<GridRowSelectionModel>([]);
  const submitButton = useRef<HTMLButtonElement>(null);

  const [hasSelected, setHasSelected] = useState(
    searchParams.get("selected") !== ""
  );
  const queryClient = useQueryClient();

  const { data, isLoading } = useQuery({
    queryKey,
    queryFn: async () =>
      await myAxios.get(
        `/reference/get-rates?ratesSearch=${
          searchParams.get("ratesSearch") as string
        }`,
        {
          headers: {
            Authorization: `Bearer ${user?.accessToken}`,
          },
        }
      ),
    onSuccess: (res) => {
      setRows((res as any)?.data?.rate?.rate);
    },
  });

  const {
    mutate,
    isLoading: loadingAddNew,
    variables,
  } = useMutation({
    mutationKey: queryKey,
    mutationFn: async (variables: any) => {
      if (variables.mode && variables.mode === "delete") {
        return await myAxios.post(
          "/reference/delete-rates",
          { ID: searchParams.get("selected") },
          {
            headers: {
              Authorization: `Bearer ${user?.accessToken}`,
            },
          }
        );
      }
      if (hasSelected) {
        delete variables.mode;
        return await myAxios.post(
          "/reference/update-rates",
          { ...variables, ID: searchParams.get("selected") },
          {
            headers: {
              Authorization: `Bearer ${user?.accessToken}`,
            },
          }
        );
      }
      delete variables.mode;
      return await myAxios.post("/reference/add-rates", variables, {
        headers: {
          Authorization: `Bearer ${user?.accessToken}`,
        },
      });
    },
    onSuccess: (res) => {
      if (res.data.success) {
        queryClient.invalidateQueries(queryKey);
        return Swal.fire({
          position: "center",
          icon: "success",
          title: res.data.message,
          showConfirmButton: false,
          timer: 1500,
        });
      }

      Swal.fire({
        position: "center",
        icon: "error",
        title: res.data.message,
        showConfirmButton: false,
        timer: 1500,
      });
    },
  });

  useEffect(() => {
    if (searchParams.get("selected") === "") {
      return;
    }
    const selectedParams = searchParams.get("selected") as string;
    const selectedIDs = new Set([selectedParams]);
    const selectedRowData = rows?.filter((row: any) =>
      selectedIDs.has(row.ID.toString())
    )[0];
    if (selectedRowData) {
      setNewStateValue(dispatch, selectedRowData);
    }
  }, [rows, searchParams]);

  function handleSearchChange(event: any) {
    setLoadingSearch(true);
    const newInputValue = event.target.value;
    setSearchParams((prev) => {
      prev.set("ratesSearch", newInputValue);
      return prev;
    });

    myAxios
      .get(`/reference/search-rates?ratesSearch=${newInputValue}`, {
        headers: { Authorization: `Bearer ${user?.accessToken}` },
      })
      .then((res) => {
        console.log(res);
        if (res.data.success) {
          setLoadingSearch(false);
          setRows(res.data.rates);
        }
      });
  }

  const handleInputChange = (e: any) => {
    const { name, value } = e.target;
    if (name === "Line") {
      dispatch({ type: "UPDATE_FIELD", field: "Type", value: "" });
    }
    dispatch({ type: "UPDATE_FIELD", field: name, value });
  };

  function handleOnSave() {
    Swal.fire({
      title: "Are you sure?",
      text: "You won't be able to revert this!",
      icon: "warning",
      showCancelButton: true,
      confirmButtonColor: "#3085d6",
      cancelButtonColor: "#d33",
      confirmButtonText: "Yes, save it!",
    }).then((result) => {
      if (result.isConfirmed) {
        wait(300).then(() => {
          submitButton.current?.click();
        });
      }
    });
  }

  async function handleSubmit(e: FormEvent) {
    e.preventDefault();
    console.log(parseFloat(state.Rate).toFixed(4));
    state.Rate = parseFloat(state.Rate).toFixed(4);
    mutate({ ...state, mode: "" });
  }

  const typeSelection = (data as any)?.data?.rate?.type[state.Line];
  const handleInputChangeThrottled = throttle(handleSearchChange, 300);
  return (
    <React.Fragment>
      <Box>
        <Typography variant="h5" sx={{ marginBottom: "10px" }}>
          Rates Details
        </Typography>
      </Box>
      <Box
        sx={(theme) => ({
          display: "flex",
          alignItems: "center",
          columnGap: "20px",
          [theme.breakpoints.down("sm")]: {
            flexDirection: "column",
            alignItems: "flex-start",
            flex: 1,
            marginBottom: "15px",
          },
        })}
      >
        <div
          style={{
            marginTop: "10px",
            marginBottom: "12px",
            flex: 1,
            width: "100%",
          }}
        >
          <TextField
            label="Search"
            fullWidth
            size="small"
            type="text"
            value={searchParams.get("ratesSearch")}
            onChange={handleInputChangeThrottled}
          />
        </div>
        {exportMode ? (
          <div
            style={{
              display: "flex",
              alignItems: "center",
              columnGap: "5px",
            }}
          >
            <Button
              variant="outlined"
              color="success"
              size="small"
              onClick={() => {
                import("../../../lib/export").then((m) => {
                  m.exportFunction(
                    `/reference/export-rates?ratesSearch=${searchParams.get(
                      "ratesSearch"
                    )}&isAll=true`,
                    `Rate-All.xlsx`,
                    myAxios,
                    user
                  );
                });
              }}
            >
              Export All
            </Button>
            <Button
              variant="outlined"
              color="success"
              onClick={() => {
                import("../../../lib/export").then((m) => {
                  m.exportFunction(
                    `/reference/export-rates?ratesSearch=${searchParams.get(
                      "ratesSearch"
                    )}&isAll=false`,
                    `Rate-Search.xlsx`,
                    myAxios,
                    user
                  );
                });
              }}
              size="small"
            >
              Export Table
            </Button>
            <Button
              variant="outlined"
              color="error"
              size="small"
              onClick={() => setExportMode(false)}
            >
              Cancel
            </Button>
          </div>
        ) : (
          <div
            style={{
              display: "flex",
              alignItems: "center",
              columnGap: "20px",
            }}
          >
            <div
              style={{
                display: "flex",
                alignItems: "center",
                columnGap: "5px",
              }}
            >
              <div>
                <Dropdown setExportMode={setExportMode} />
              </div>
              {!save && (
                <Button
                  variant="contained"
                  startIcon={<AddIcon />}
                  id="entry-header-save-button"
                  onClick={() => {
                    setAddNew(true);
                    setSave(true);
                    setHasSelected(false);
                    setSearchParams((prev) => {
                      prev.set("sublineSearch", "");
                      prev.set("selected", "");
                      return prev;
                    });
                    document
                      .getElementById("reset-data-grid-selection")
                      ?.click();
                  }}
                >
                  New
                </Button>
              )}

              <Box sx={{ position: "relative" }}>
                <Button
                  id="save-entry-header"
                  color="primary"
                  variant="contained"
                  type="submit"
                  onClick={handleOnSave}
                  disabled={
                    (loadingAddNew && variables.mode === undefined) || !save
                  }
                  startIcon={<SaveIcon />}
                >
                  Save
                </Button>

                {loadingAddNew && variables.mode === undefined && (
                  <CircularProgress
                    size={24}
                    sx={{
                      color: green[500],
                      position: "absolute",
                      top: "50%",
                      left: "50%",
                      marginTop: "-12px",
                      marginLeft: "-12px",
                    }}
                  />
                )}
              </Box>
              {save && (
                <Button
                  variant="contained"
                  startIcon={<CloseIcon />}
                  color="error"
                  onClick={() => {
                    Swal.fire({
                      title: "Are you sure?",
                      text: "You won't be able to revert this!",
                      icon: "warning",
                      showCancelButton: true,
                      confirmButtonColor: "#3085d6",
                      cancelButtonColor: "#d33",
                      confirmButtonText: "Yes, cancel it!",
                    }).then((result) => {
                      if (result.isConfirmed) {
                        setSave(false);
                        setAddNew(false);
                        setDeleting(false);
                        setNewStateValue(dispatch, initialState);
                        document
                          .getElementById("reset-data-grid-selection")
                          ?.click();
                        setHasSelected(false);
                        setSearchParams((prev) => {
                          prev.set("sublineSearch", "");
                          prev.set("selected", "");
                          return prev;
                        });
                      }
                    });
                  }}
                >
                  Cancel
                </Button>
              )}

              <Button
                id="save-entry-header"
                variant="contained"
                sx={{
                  backgroundColor: pink[500],
                  "&:hover": {
                    backgroundColor: pink[600],
                  },
                }}
                disabled={!deleting}
                startIcon={<DeleteIcon />}
                onClick={() => {
                  Swal.fire({
                    title: "Are you sure?",
                    text: "You won't be able to revert this!",
                    icon: "warning",
                    showCancelButton: true,
                    confirmButtonColor: "#3085d6",
                    cancelButtonColor: "#d33",
                    confirmButtonText: "Yes, delete it!",
                  }).then((result) => {
                    if (result.isConfirmed) {
                      mutate({
                        ID: searchParams.get("selected") as string,
                        mode: "delete",
                      });
                    } else {
                    }
                  });
                }}
              >
                Delete
              </Button>
            </div>
          </div>
        )}
      </Box>
      {!exportMode && (
        <form onSubmit={handleSubmit}>
          <Box
            sx={(theme) => ({
              display: "flex",
              columnGap: "15px",
              flexDirection: "row",
              [theme.breakpoints.down("md")]: {
                flexDirection: "column",
                rowGap: "10px",
              },
            })}
          >
            {isLoading ? <LinearProgress/>: (
              <React.Fragment>
                <FormControl
                  size="small"
                  fullWidth
                  disabled={!addNew || hasSelected}
                  required
                >
                  <InputLabel id="Line">Line</InputLabel>
                  <Select
                    labelId="Line"
                    value={state.Line}
                    label="Line"
                    name="Line"
                    onChange={handleInputChange}
                  >
                    {(data as any).data?.rate?.line.map(
                      (items: any, idx: number) => {
                        return (
                          <MenuItem key={idx} value={items.Line}>
                            {items.Line}
                          </MenuItem>
                        );
                      }
                    )}
                  </Select>
                </FormControl>

                <FormControl
                  size="small"
                  fullWidth
                  disabled={!addNew || hasSelected}
                  required
                >
                  <InputLabel id="Account">Account</InputLabel>
                  <Select
                    labelId="Account"
                    value={state.Account}
                    label="Account"
                    name="Account"
                    onChange={handleInputChange}
                  >
                    {(data as any).data?.rate?.policy.map(
                      (items: any, idx: number) => {
                        return (
                          <MenuItem key={idx} value={items.Account}>
                            {items.Account}
                          </MenuItem>
                        );
                      }
                    )}
                  </Select>
                </FormControl>
                {typeSelection ? (
                  <FormControl
                    size="small"
                    fullWidth
                    disabled={!addNew}
                    required
                  >
                    <InputLabel id="Type">Type</InputLabel>
                    <Select
                      labelId="Type"
                      value={state.Type}
                      label="Type"
                      name="Type"
                      onChange={handleInputChange}
                    >
                      {typeSelection.map((items: any, idx: number) => {
                        return (
                          <MenuItem key={idx} value={items.SublineName}>
                            {items.SublineName}
                          </MenuItem>
                        );
                      })}
                    </Select>
                  </FormControl>
                ) : (
                  <TextField
                    fullWidth
                    type="text"
                    variant="outlined"
                    size="small"
                    label="Type"
                    name="Type"
                    value={state.Type}
                    onChange={handleInputChange}
                    disabled={!addNew}
                    required
                  />
                )}
              </React.Fragment>
            )}
            <TextField
              required
              fullWidth
              type="text"
              variant="outlined"
              size="small"
              label="Rate"
              name="Rate"
              value={state.Rate}
              onChange={handleInputChange}
              disabled={!addNew}
            />
          </Box>
          <button
            ref={submitButton}
            style={{ display: "none" }}
            type="submit"
          ></button>
        </form>
      )}
      <div style={{ marginTop: "10px", width: "100%", position: "relative" }}>
        <DataGridTable
          mutipleSelect={exportMode}
          isLoading={loadingAddNew || isLoading || loadingSearch}
          dataKey={"entry"}
          queryKey={[queryKey]}
          columns={sublineColumn}
          onSelectionChange={(rowSelectionModel, data) => {
            if (rowSelectionModel.length <= 0) {
              setNewStateValue(dispatch, initialState);
              setSearchParams((prev) => {
                prev.set("selected", "");
                return prev;
              });
              setHasSelected(false);
              setAddNew(false);
              setSave(false);
              setDeleting(false);
              return;
            }
            setSave(true);
            setDeleting(true);
            setAddNew(true);
            setHasSelected(true);

            const selectedIDs = new Set(rowSelectionModel);
            const selectedRowData = data.filter((row: any) =>
              selectedIDs.has(row.ID.toString())
            );
            setSearchParams((prev) => {
              prev.set("selected", rowSelectionModel[0] as string);
              return prev;
            });

            setNewStateValue(dispatch, selectedRowData[0]);
          }}
          getRowId={(row) => row.ID.toString()}
          rows={rows}
          setRows={setRows}
        />
      </div>
    </React.Fragment>
  );
}
export function setNewStateValue(dispatch: any, obj: any) {
  Object.entries(obj).forEach(([field, value]) => {
    dispatch({ type: "UPDATE_FIELD", field, value });
  });
}
